.lc-history-info {
	.lc-history__top {
		ul {
			li {
				width: 15%;

				&:first-child {
					width: 70%;
				}
			}
		}
	}
	.lc-history__block {
		padding: 0;
		width: 100%;
		background: linear-gradient(91.64deg, #FFFFFF 62.97%, rgba(255, 255, 255, 0) 106.73%);

		&-info {
			&__name {
				width: 100%;
				display: flex;
				justify-content: space-between;
				padding: 17px 30px;
				border-bottom: 1px solid #E8ECEC;
				background-color: rgba(248, 251, 251, 1);

				.num {
					font-weight: 600;
					font-size: 18px;
					line-height: 135%;
					color: #F74B4B;
				}

				.price {
					font-weight: 600;
					font-size: 16px;
					line-height: 135%;
					color: #282828;
				}
			}

			&__item {
				width: 100%;
				display: flex;
				flex-wrap: wrap;
				border-bottom: 1px solid #E8ECEC;
				&:last-child{
					border-bottom: 0;
				}

				&-img {
					padding: 50px;
					width: 30%;
					position: relative;
					min-height: 300px;
					.bg {
						position: absolute;
						left: 50px;
						top: 50px;
						width: calc(100% - 100px);
						height: calc(100% - 100px);
						background-size: contain;
						background-position: center;
						background-repeat: no-repeat;

					}
				}

				&-text {
					width: 70%;
					display: flex;
					flex-wrap: wrap;
					padding: 40px 0;

					.text {
						width: 54%;

						.name {
							font-weight: 600;
							font-size: 16px;
							line-height: 135%;
							color: #1E1E1E;
							margin-bottom: 20px;
						}

						ul {
							li {
								margin-bottom: 10px;
								line-height: 1;

								&:last-child {
									margin-bottom: 0;
								}

								span {
									line-height: 173%;
									font-size: 15px;
									color: #373737;

									&:nth-child(1) {
										font-weight: 600;
										margin-right: 6px;
									}

									&:nth-child(2) {

									}
								}
							}
						}
					}
				}

				&-num {
					width: 23%;
					text-align: center;
					border-left: 1px solid #E8ECEC;
					.name{
						display: none;
					}
					span {
						font-weight: 600;
						font-size: 14px;
						line-height: 135%;
						color: #4F5050;
					}
				}

				&-link {
					width: 100%;
					margin-top: 25px;
					display: flex;
					justify-content: space-between;
					align-items: center;
					padding-right: 30px;
					.link--gray{
						margin-right: 14px;
					}
				}
			}

		}
	}
}

.link--gray {
	padding: 12px 19px;
	font-weight: 500;
	font-size: 14px;
	line-height: 135%;
	color: #4D585A;
	border: 2px solid #546062;
	border-radius: 5px;
	text-transform: uppercase;
	&:hover{
		border-color: #979898;
	}
}
.link--refresh{
	padding: 8.5px 15px;
	border: 2px solid #F74B4B;
	border-radius: 5px;
	color: #F74B4B;
	display: flex;
	align-items: center;
	text-transform: uppercase;
	font-weight: 500;
	font-size: 14px;
	line-height: 135%;
	transition: .5s;
	svg{
		fill: #F74B4B;
		width: 25px;
		height: 25px;
		margin-right: 15px;
		transition: .5s;
	}
	&:hover{
		color: #fff;
		background-color: #F74B4B;
		svg{
			fill: #fff;
			transform: rotate(180deg);
		}
	}
}

.lc-history-info-reserve{
	.lc-history__block-info__name .num{
		color: #414141;
	}
	.pagination{
		padding-bottom: 0;
	}
	.lc-history__top{
		ul{
			margin-top: 0;
			li{
				padding-top: 10px;
			}
		}
	}
	.link--refresh{
		&:hover svg{
			transform: none;
		}
	}
	.btn-to-basket{
		color: #F74B4B;
		display: flex;
		align-items: center;
		cursor: pointer;
		transition: .3s;
		&:hover{
			color: #000000;
			svg{
				fill:  #000000;
			}
		}
		svg{
			margin-right: 15px;
			fill: #F74B4B;
			transition: .3s;
		}
	}
	.lc-history__block-info__item-link{
		padding-right: 0;
		div{
			width: 100%;
			display: flex;
			flex-wrap: wrap;
			a{
				margin-right: 10px;
				&:last-child{
					margin-right: 0;
				}
			}
		}
	}
}